<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-其他日期单位1" class="vue-doc-element">其他日期单位</h2>
<p class="vue-doc-element">通过扩展基础的日期选择，可以选择周、月、年或多个日期</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          
        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>

  <span>多个日期</span>
  <DatePicker
    type="dates"
    v-model="value14"
    placeholder="选择一个或多个日期">
  </DatePicker>
  <span>周</span>
  <DatePicker
    v-model="value3"
    type="week"
    format="yyyy 第 WW 周"
    placeholder="选择周">
  </DatePicker>

  <span>月</span>
  <DatePicker
    v-model="value4"
    type="month"
    placeholder="选择月">
  </DatePicker>

  <span>年</span>
  <DatePicker
    v-model="value5"
    type="year"
    placeholder="选择年">
  </DatePicker>

</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>多个日期<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">DatePicker</span>
    <span class="hljs-attr">type</span>=<span class="hljs-string">"dates"</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value14"</span>
    <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择一个或多个日期"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">DatePicker</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>周<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">DatePicker</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value3"</span>
    <span class="hljs-attr">type</span>=<span class="hljs-string">"week"</span>
    <span class="hljs-attr">format</span>=<span class="hljs-string">"yyyy 第 WW 周"</span>
    <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择周"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">DatePicker</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>月<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">DatePicker</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value4"</span>
    <span class="hljs-attr">type</span>=<span class="hljs-string">"month"</span>
    <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择月"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">DatePicker</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>年<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">DatePicker</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value5"</span>
    <span class="hljs-attr">type</span>=<span class="hljs-string">"year"</span>
    <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择年"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">DatePicker</span>&gt;</span>

<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>


<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">value3</span>: <span class="hljs-string">''</span>,
        <span class="hljs-attr">value4</span>: <span class="hljs-string">''</span>,
        <span class="hljs-attr">value5</span>: <span class="hljs-string">''</span>,
        <span class="hljs-attr">value14</span>: []
      };
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
  
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
    data() {
      return {
        value3: '',
        value4: '',
        value5: '',
        value14: []
      };
    }
  };
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


